/*------------------ Navbar Section ------------------------------*/

.menu-wrap {
  position: fixed;
  top: 2%;
  left: 2%;
  z-index: 10;
}

.menu-wrap .toggler {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 20;
  cursor: pointer;
  width: 50px;
  height: 50px;
  opacity: 0;
}

.menu-wrap .hamburger {
  position: absolute;
  z-index: 10;
  width: 60px;
  height: 60px;
  padding: 1rem;
  background: #1a1a1a;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
}

/* Hamburger Line */
.menu-wrap .hamburger > div {
  position: relative;
  flex: none;
  width: 100%;
  height: 2px;
  background: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.4s ease;
}

/* Hamburger Lines - Top & Bottom */
.menu-wrap .hamburger > div::before,
.menu-wrap .hamburger > div::after {
  content: "";
  position: absolute;
  z-index: 20;
  top: -10px;
  width: 100%;
  height: 2px;
  background: inherit;
}

/* Moves Line Down */
.menu-wrap .hamburger > div::after {
  top: 10px;
}

/* Toggler Animation */
.menu-wrap .toggler:checked + .hamburger > div {
  transform: rotate(135deg);
}

/* Turns Lines Into X */
.menu-wrap .toggler:checked + .hamburger > div:before,
.menu-wrap .toggler:checked + .hamburger > div:after {
  top: 0;
  transform: rotate(90deg);
}

/* Show Menu */
.menu-wrap .toggler:checked ~ .menu {
  visibility: visible;
}

.menu-wrap .toggler:checked ~ .menu > div {
  transform: scale(1);
  transition-duration: 1s;
}

.menu-wrap .toggler:checked ~ .menu > div > div {
  opacity: 1;
  transition: opacity 0.4s ease 0.4s;
}

.menu-wrap .menu {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  visibility: hidden;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
}

.menu-wrap .menu > div {
  background: rgba(0, 0, 0, 0.96);
  border-radius: 50%;
  width: 200vw;
  height: 200vw;
  display: flex;
  flex: none;
  align-items: center;
  justify-content: center;
  transform: scale(0);
  transition: all 0.4s ease;
}

.menu-wrap .menu > div > div {
  text-align: center;
  max-width: 90vw;
  max-height: 100vh;
  opacity: 0;
  transition: opacity 0.4s ease;
}

.menu-wrap .menu > div > div > ul > li {
  list-style: none;
  font-size: 1.7rem;
  padding: 3.2%;
}

.menu-wrap .menu > div > div > ul > li > a {
  color: #babdc0;
  font-family: "Raleway", sans-serif;
  text-decoration: none;
  /* font-weight: 500; */
  transition: color 0.4s ease;
}

.menu-wrap .menu > div > div > ul > li > a:hover,
.menu-wrap .menu > div > div > ul > li > a:focus {
  color: #ffffff;
}

.menu-wrap .menu > div > div > ul > li > a:hover:after {
  content: "";
  display: block;
  width: 50px;
  border-bottom: 2px solid #ffffff;
  margin: -2px auto;
  border-radius: 100px;
}

/*-------------------------------------- Landing Page  -------------------------------------- */

/*play button */
.btn-container {
  position: absolute;
  left: 0;
  right: 0;
  margin-left: auto;
  margin-right: auto;
  top: 55%;
  z-index: 10;
  width: 100px; /* Need a specific value to work */
}
.bg,
.buttons {
  position: absolute;
  width: 85px;
  height: 85px;
  border-radius: 100%;
}

.bg {
  animation: pulse 1.2s ease infinite;
  background: rgb(107, 25, 76);
}

.buttons {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  border: none;
  background: linear-gradient(#d42056, #58144f);
  cursor: pointer;
}

.buttons i {
  position: absolute;
  color: #fff;
  font-size: 30px;
}

@keyframes pulse {
  0% {
    transform: scale(1, 1);
  }
  50% {
    opacity: 0.3;
  }
  100% {
    transform: scale(1.5);
    opacity: 0;
  }
}

/* mute button */

.btn-mute {
  position: absolute;
  top: 4%;
  cursor: pointer;
  right: 3%;
  background-color: #1a1a1a;
  border: none;
  outline: none;
}
.btn-mute:focus {
  outline: none;
}
.mutess i {
  cursor: pointer;
  font-size: 40px;
  color: white;
}

/* main div */

.animation-container {
  height: 110vh;
  background-color: #1a1a1a;
  position: relative;
}
.wave-pattern {
  position: absolute;
  bottom: 0;
  top: -3px;
  left: 0;
  width: 100%;
  overflow: hidden;
  line-height: 0;
  transform: rotate(180deg);
}

.wave-pattern svg {
  position: relative;
  display: block;

  width: calc(135% + 1.3px);
  height: 120px;
}

.wave-pattern .shape-fill {
  fill: #ffffff;
}

/** For tablet devices **/
@media (min-width: 768px) and (max-width: 1023px) {
  .wave-pattern svg {
    width: calc(160% + 1.3px);
    height: 150px;
  }
}

/** For mobile devices **/
@media (max-width: 767px) {
  .wave-pattern svg {
    width: calc(174% + 1.3px);
    height: 100px;
  }
}

