/*-------------------------------------- Event format --------------------------------------*/
.event-format-row {
  margin-top: 5%;
}
.event-format {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  /* justify-content: space-between; */
}
.event-format-heading {
  margin-top: -8%;
  padding-bottom: 1%;
}
.phase {
  margin: 1%;
