  padding-top: 5%;
}
/*--------------------------------- Problem Statement ---------------------------------*/
.problem-stmt-row {
  margin: 5%;
}
.card-container {
  position: relative;
  perspective: 200rem;
  height: 330px;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}

.card {
  position: absolute;
  transition: all 1.5s;
  backface-visibility: hidden;
  border-radius: 10px;
  box-shadow: rgba(143, 129, 119, 0.2) 5px 10px 20px;
}
.card-front {
  background-color: #1a1a1a;
  height: 80%;
  width: 63%;
}

.card-back {
  padding: 5%;
  transform: rotateY(180deg);
  height: 80%;
  width: 63%;
}
.view-sample-stmt-btn {
  width: 100%;
  font-size: 15px;
  background-color: #1a1a1a;
  color: white;
}
.card-container:hover .card-front {
  transform: rotateY(180deg);
  /* border-radius: 100%; */
}

.card-container:hover .card-back {
  transform: rotateY(0deg);
  /* border-radius: 100%; */
}

.card-back .card-body {
  position: relative;
}

